import './App.css';
import React, { Component } from 'react';
import Properties from './ContentViews/LogProperties.js';
import PilotLog from './ContentViews/PilotLog.js';
import InstructorView from './ContentViews/InstructorView.js';
import FileInput from './FileInput.js';
import PilotRoles from './PilotRoles.js';

class App extends Component {

    constructor(props) {
        super(props);
        const start = new Date()
        this.state = {
          selectedView: 0,
          log:
              { properties :
                { name:"Simulation",
                  configuration:"WL",
                  description:"",
                  objectives:"",
                  traffic_density:1,
                  weather:"",
                  duration:60,
                  start_date:start.toJSON()
                },
                instructor_log: {
                  setup_info:"",
                  events:[]
                },
                pilot_logs : [
                ]
              }
           };
        this.openFile = this.openFile.bind(this);
    }
    
    handlePropertiesChange = (properties) => {
        this.setState({ log: {properties: properties, instructor_log:this.state.log.instructor_log, pilot_logs:this.state.log.pilot_logs} });
    }
    
    handleInstructorLogChange = (instructor_log) => {
        this.setState({ log: {properties:this.state.log.properties, instructor_log: instructor_log, pilot_logs:this.state.log.pilot_logs} });
    }
    
    handlePilotLogChange = (pilot_log, index) => {
      var pilot_logs = this.state.log.pilot_logs
      pilot_logs[index] = pilot_log
        this.setState({ log: {properties:this.state.log.properties, instructor_log: this.state.log.instructor_log, pilot_logs:pilot_logs} });
    }
  
    handlePilotLogsChange = (logs) => {
        this.setState({ log: {properties:this.state.log.properties, instructor_log: this.state.log.instructor_log, pilot_logs:logs} });
    }

    handlePilotLogDelete = (log) => {
      var pilot_logs = this.state.log.pilot_logs;
      var selectedView = this.state.selectedView;
      if (selectedView == pilot_logs.length + 2) {
       selectedView = selectedView - 1;
      }
      pilot_logs.splice(pilot_logs.indexOf(log),1);
      this.setState({selectedView:selectedView, log: {properties:this.state.log.properties, instructor_log: this.state.log.instructor_log, pilot_logs:pilot_logs} });
    }

    openFile(file) {
      const handleFileRead = (e) => {
        const content = fileReader.result;
        var log = JSON.parse(content)
        log.properties.start_date = new Date(Date.parse(log.properties.start_date));
        this.setState({log:log})
      };

      let fileReader = new FileReader();
      fileReader.onloadend = handleFileRead;
      fileReader.readAsText(file);
    }

    saveFile = () => {
      var log = this.state.log

      // Set update date
      log.properties.update_date = new Date().toISOString().slice(0,-5)+"Z"

      var json = JSON.stringify(log, null, ' ')
      var a = document.createElement("a");
      var file = new Blob([json], {type: "text/json"});
      a.href = URL.createObjectURL(file);
      a.download = log.properties.name + ".simlog";
      a.click();
    }

    showProperties = () => this.setState({ selectedView: 0 });
    showInstructor = () => this.setState({ selectedView: 1 });
    showPositions = () => this.setState({ selectedView: 2 });
    showPilotLog = (index) => this.setState({ selectedView: 3 + index });
    
    content() {
      if (this.state.selectedView === 0) {
        // Simulation properties
        return (<Properties properties={this.state.log.properties} onChange={this.handlePropertiesChange} />)
      } else if (this.state.selectedView === 1) {
        // Instructor log
        return (<InstructorView instructor_log={this.state.log.instructor_log} onChange={this.handleInstructorLogChange} />)
      } else if (this.state.selectedView === 2) {
        // Control positions
        return (null)
      } else {
        var logIndex = this.state.selectedView - 3
        var pilotLog = this.state.log.pilot_logs[logIndex]
        return <PilotLog pilotLog={pilotLog} index={logIndex} onChange={this.handlePilotLogChange}
              onDelete={this.handlePilotLogDelete} />
      }
    }
    
    render() {
      var log = this.state.log
      var date = new Date(log.properties.start_date)
      log.properties.start_date = date.toISOString().slice(0,-5)+"Z"
      var json = JSON.stringify(log, null, ' ')

      var selectedRole = this.state.selectedView - 3

      const handleFileInput = (event) => {
        var file = event.target.files[0];
        this.openFile(file)
      }

      return (
        <div id="app">
          <div id="sidebar">
            <div>
              <ul>
                <li><button onClick={this.showProperties} className={`${this.state.selectedView === 0 ? "current" : ""}`}>Propriétés</button></li>
                <li><button onClick={this.showInstructor} className={`${this.state.selectedView === 1 ? "current" : ""}`}>Instructeur</button></li>
                <li><button onClick={this.showPositions} className={`${this.state.selectedView === 2 ? "current" : ""}`}>Positions</button></li>
              </ul>
              <PilotRoles pilotLogs={this.state.log.pilot_logs}
              onClick={this.showPilotLog}
              selectedRole={selectedRole}
              onChange={this.handlePilotLogsChange}/>
            </div>
            <div>
              <ul>
              <li><label><input type="file" accept=".simlog" onChange={handleFileInput}/><span>Importer…</span></label></li>
              <li><button className="action" onClick={this.saveFile}>Enregistrer</button></li>
              </ul>
            </div>
          </div>
          <div id="content">
            {this.content()}
          </div>
        </div>
      );
    }
}

export default App;
