import React, { Component } from 'react';
import TextField from './TextField.js';
import TextArea from './TextArea.js';

class SetupElementView extends Component {

  handleSelectedEventCallsignChange = (callsign) => {
    this.props.setupElement.callsign = callsign;
    this.props.onChange(this.props.setupElement);
  }

  handleSelectedEventDescriptionChange = (description) => {
    this.props.setupElement.description = description;
    this.props.onChange(this.props.setupElement);
  }

  render() {
    if (this.props.setupElement) {
      return <div className="two-columns">
              <TextField label="Indicatif" id="selected-setup-callsign" value={this.props.setupElement.callsign} onChange={this.handleSelectedEventCallsignChange} />
              <TextField label="Situation initiale" id="selected-setup-description" value={this.props.setupElement.description} onChange={this.handleSelectedEventDescriptionChange} />
            </div>
    } else {
      return <p>Aucun évènement sélectionné</p>
    }
  }

}

class SetupView extends Component {
  constructor(props) {
      super(props);
      this.state = { selectedSetupElement:null }
  }


  componentWillReceiveProps(nextProps) {
    var nextSetup = nextProps.setup || []
    var setup = this.props.setup || []
    if (nextSetup.every(event => setup.includes(event)) == false) {
     this.setState({selectedEvent : null})
    }
  }


  selectSetupElement = (event) => this.setState({ selectedSetupElement: event });

  handleSelectedSetupElementChange = (setupElement) => {
    this.setState({selectedSetupElement : setupElement})
    this.props.onChange(this.props.setup);
  }

  addSetupElement = () => {
    let setupElements = this.props.setup
    let newSetupElement = {callsign:"", description:""}
    setupElements.push(newSetupElement)
    this.props.onChange(setupElements)
    this.setState({selectedSetupElement:newSetupElement})
  }

  deleteSelectedSetupElement = () => {
    if(this.state.selectedSetupElement !== null) {
      let setupElements = this.props.setup
      let selectionIndex = setupElements.indexOf(this.state.selectedSetupElement)
      delete setupElements[setupElements.indexOf(this.state.selectedSetupElement)]
      this.props.onChange(setupElements)
      if (selectionIndex > 0) {
        this.selectSetupElement(this.props.setup[selectionIndex - 1])
      } else {
        this.setState({selectedSetupElement:null})
      }
    }
  }
    
  render() {
    var setup = this.props.setup || []
    return <div>
      <div className="toolbar">
        <button className="action" onClick={this.addSetupElement}>Ajouter</button>
        <button className={`action${this.state.selectedSetupElement == null ? " disabled" : ""}`} onClick={this.deleteSelectedSetupElement}>Supprimer</button>
      </div>
      <div className="master">
        <table>
          <thead>
            <tr>
              <th>
                Indicatif
              </th>
              <th>
                Situation initiale
              </th>
            </tr>
          </thead>
          <tbody>
        {setup.map(setupElement => {
          return <tr onClick={() => this.selectSetupElement(setupElement)} className={`${this.state.selectedSetupElement === setupElement ? " selected" : ""}`}>
                  <td>
                    {setupElement.callsign}
                  </td>
                  <td>
                    {setupElement.description}
                  </td>
                </tr>
        })}
          </tbody>
        </table>
      </div>
      <div className="detail">
       <SetupElementView setupElement={this.state.selectedSetupElement} onChange={this.handleSelectedSetupElementChange} />
      </div>
      </div>
  }
}

export default SetupView;
