import React, { Component } from 'react';
import TextField from '../TextField.js';
import ConfigurationPicker from '../ConfigurationPicker.js';
import TrafficDensityPicker from '../TrafficDensityPicker.js';
import TextArea from '../TextArea.js';

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { registerLocale } from "react-datepicker";
import fr from 'date-fns/locale/fr';
registerLocale('fr', fr)

class Properties extends Component {
    constructor(props) {
        super(props);
        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleConfigurationChange = this.handleConfigurationChange.bind(this);
        this.handleTrafficDensityChange = this.handleTrafficDensityChange.bind(this);
        this.handleWeatherChange = this.handleWeatherChange.bind(this);
        this.handleDurationChange = this.handleDurationChange.bind(this);
        this.handleDescriptionChange = this.handleDescriptionChange.bind(this);
        this.handleObjectivesChange = this.handleObjectivesChange.bind(this);
    }
    
    handleNameChange(newName) {
        this.props.properties.name = newName;
        this.props.onChange(this.props.properties);
    }
    
    handleConfigurationChange(newConfiguration) {
        this.props.properties.configuration = newConfiguration;
        this.props.onChange(this.props.properties);
    }
    
    handleTrafficDensityChange(density) {
        this.props.properties.traffic_density = parseInt(density)
        this.props.onChange(this.props.properties);
    }
    
    handleWeatherChange(weather) {
        this.props.properties.weather = weather
        this.props.onChange(this.props.properties);
    }
    
    handleDurationChange(duration) {
        this.props.properties.duration = parseInt(duration)
        this.props.onChange(this.props.properties);
    }
    
    handleDescriptionChange(description) {
        this.props.properties.description = description;
        this.props.onChange(this.props.properties);
    }
    
    handleObjectivesChange(objectives) {
        this.props.properties.objectives = objectives;
        this.props.onChange(this.props.properties);
    }

    handleChange = (date, event) => {
        this.props.properties.start_date = date;
        this.props.onChange(this.props.properties);
    };
    
  render() {
    return <div>
      <h1>{this.props.properties.name}</h1>
      <div className="two-columns">
      <div>
      <TextField label="Nom" id="simulation-name" value={this.props.properties.name} onChange={this.handleNameChange} /><br />
      <ConfigurationPicker value={this.props.properties.configuration} onChange={this.handleConfigurationChange} /><br />
      <TrafficDensityPicker value={this.props.properties.traffic_density} onChange={this.handleTrafficDensityChange} /><br />
      <TextField label="Météo" id="simulation-weather" tooltip="Entrez la météo au format METAR. Ex : 12010KT Q1013 9999 SCT025" value={this.props.properties.weather} onChange={this.handleWeatherChange} /><br />
      <TextField label="Durée" id="simulation-duration" value={this.props.properties.duration} onChange={this.handleDurationChange} /><br />
      </div>
      <div>
      <TextArea label="Description" value={this.props.properties.description} onChange={this.handleDescriptionChange} /><br />
      <TextArea label="Objectifs" value={this.props.properties.objectives} onChange={this.handleObjectivesChange} /><br />
      
      </div>
      </div>
      </div>
  }
}

export default Properties;
