import React, { Component } from 'react';
import TextArea from '../TextArea.js';
import EventsView from '../EventsView.js';

const tabs = {
  setup_info: 0,
  events:1
};

class InstructorView extends Component {

  constructor(props) {
      super(props);
      this.state = {
        currentView:tabs.setup_info
      }
  }
  
  handleSetupInfoChange = (setup_info) => {
      this.props.instructor_log.setup_info = setup_info;
      this.props.onChange(this.props.instructor_log);
  }
  
  handleEventsChange = (events) => {
      this.props.instructor_log.events = events;
      this.props.onChange(this.props.instructor_log);
  }

  showSetupInfo = () => this.setState({ currentView: tabs.setup_info });
  showEvents = () => this.setState({ currentView: tabs.events });

  tabItem() {
    switch (this.state.currentView) {
      case tabs.setup_info:
      return <TextArea label="Situation initiale" value={this.props.instructor_log.setup_info} onChange={this.handleSetupInfoChange} />
      case tabs.events:
      return <EventsView events={this.props.instructor_log.events} onChange={this.handleEventsChange}/>
      default:
      return (null)
    }
  }

  render() {
    return <div>
      <h1>Instructeur</h1>
      <ul className="tabButton">
          <li onClick={this.showSetupInfo} className={`tablink${this.state.currentView === tabs.setup_info ? " current" : ""}`}>Mise en place</li>
          <li onClick={this.showEvents} className={`tablink${this.state.currentView === tabs.events ? " current" : ""}`}>Évènements</li>
      </ul>
      {this.tabItem()}
      </div>
  }
}

export default InstructorView;
