import React, { Component } from 'react';

class FileInput extends Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.fileInput = React.createRef();
  }

  handleSubmit(event) {
    event.preventDefault();
    this.props.onOpen(this.fileInput.current.files[0])
    
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          <input type="file" ref={this.fileInput} accept=".simlog" />
        </label>
        <br />
        <button className="action" type="submit">Importer</button>
      </form>
    );
  }
}

export default FileInput;
