import React, { Component } from 'react';
import TextField from '../TextField.js';
import TextArea from '../TextArea.js';
import EventsView from '../EventsView.js';
import SetupView from '../SetupView.js';


const tabs = {
  directives: 0,
  setup:1,
  events:2
};

class PilotLog extends Component {

  constructor(props) {
      super(props);
      this.state = {
        currentView:tabs.directives
      }
  }

  // Tabs

  showDirectives = () => this.setState({ currentView: tabs.directives });
  showSetup = () => this.setState({ currentView: tabs.setup });
  showEvents = () => this.setState({ currentView: tabs.events });

  tabItem() {
    switch (this.state.currentView) {
      case tabs.directives:
      return <TextArea label="Consignes" value={this.props.pilotLog.directives} onChange={this.handleDirectivesChange} />
      case tabs.setup:
      return <SetupView setup={this.props.pilotLog.setup} onChange={this.handleSetupChange}/>
      case tabs.events:
      return <EventsView events={this.props.pilotLog.events} onChange={this.handleEventsChange}/>
      default:
      return (null)
    }
  }

  // Handle changes
  
  handleRoleChange = (role) => {
      this.props.pilotLog.role = role;
      this.props.onChange(this.props.pilotLog, this.props.index);
  }
  
  handleFrequencyChange = (frequency) => {
      this.props.pilotLog.frequency = frequency;
      this.props.onChange(this.props.pilotLog, this.props.index);
  }
  
  handleDirectivesChange = (directives) => {
      this.props.pilotLog.directives = directives;
      this.props.onChange(this.props.pilotLog, this.props.index);
  }
  
  handleSetupChange = (setup) => {
      this.props.pilotLog.setup = setup;
      this.props.onChange(this.props.pilotLog, this.props.index);
  }
  
  handleEventsChange = (events) => {
      this.props.pilotLog.events = events;
      this.props.onChange(this.props.pilotLog, this.props.index);
  }

  delete = () => {
      this.props.onDelete(this.props.pilotLog);
  }

  render() {
    this.props.pilotLog.frequency = this.props.pilotLog.frequency || ""
    this.props.pilotLog.directives = this.props.pilotLog.directives || ""
    this.props.pilotLog.events = this.props.pilotLog.events || []

    return <div>
      <button className="destructive_action" onClick={this.delete}>Supprimer</button>
      <h1>{this.props.pilotLog.role}</h1>
      <div className="two-columns">
      <TextField label="Rôle" value={this.props.pilotLog.role} onChange={this.handleRoleChange} /><br />
      <TextField label="Fréquence" value={this.props.pilotLog.frequency} onChange={this.handleFrequencyChange} /><br />
      </div>
      <ul className="tabButton">
          <li onClick={this.showDirectives} className={`tablink${this.state.currentView === tabs.directives ? " current" : ""}`}>Consignes</li>
          <li onClick={this.showSetup} className={`tablink${this.state.currentView === tabs.setup ? " current" : ""}`}>Mise en place</li>
          <li onClick={this.showEvents} className={`tablink${this.state.currentView === tabs.events ? " current" : ""}`}>Évènements</li>
      </ul>
      {this.tabItem()}
      </div>
  }
}

export default PilotLog;