import React, { Component } from 'react';

class PilotRoles extends Component {

    showPilotLog = (index) => {
      this.props.onClick(index);
    };

    addPilot = () => {
      console.log("addPilot")
      var pilotLogs = this.props.pilotLogs;
      let newPilotLog = {role:"Pilote", frequency:"", directives:"", setup:[], events: []}
      pilotLogs.push(newPilotLog)
      this.props.onChange(pilotLogs)
    };

    render() {

        var roles = this.props.pilotLogs.map((log, index) => {
                return (<li key={index}><button onClick={() => this.showPilotLog(index)} className={`${this.props.selectedRole === index ? "current" : ""}`}>{log.role}</button></li>)
        });

        return (
              <ul>
                <li>Roles pilote <button className="add_pilot_button" onClick={this.addPilot}>Ajouter</button></li>
                {roles}
              </ul>
              )
    }
}

export default PilotRoles;