import React, { Component } from 'react';

class TrafficDensityPicker extends Component {
    constructor(props) {
        super(props);
        this.state = { value: props.value };

        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.props.onChange(parseInt(event.target.value));
      }

    render() {
        return (<div className="form-field-input">
                    <div className="label-row">
                        <label htmlFor="{this.props.id}">Charge</label>
                    </div>
          <select value={this.props.value} onChange={this.handleChange}>
            <option value="1">Faible</option>
            <option value="2">Modérée</option>
            <option value="3">Modérée à forte</option>
            <option value="4">Forte</option>
          </select>
                </div>
            )
    }
}

export default TrafficDensityPicker;
