import React, { Component } from 'react';

class TextField extends Component {
    state = {
        showTooltip: false
     };

    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.props.onChange(event.target.value);
      }

    tooltipButton() {
        if (this.props.tooltip != null) {
            return (<div className="tooltip-wrapper">
                <button className="icon icon-questionmark tooltip-button" type="button" onMouseEnter={this.showTooltip} onMouseLeave={this.hideTooltip}>
                </button>
                <div className={`tooltip${this.state.showTooltip ? " shown" : ""}`}>
                    {this.props.tooltip}
                </div>
            </div>)
        } else {
            return (null)
        }
    }

    showTooltip = () => this.setState({ showTooltip: true });
    hideTooltip = () => this.setState({ showTooltip: false });


    render() {
        return (<div className="form-field-input">
                    <div className="label-row">
                        <label htmlFor="{this.props.id}">{this.props.label}</label>
                        {this.tooltipButton()}  
                    </div>
                    <input type="text" value={this.props.value} onChange={this.handleChange} />
                </div>)
    }
}

export default TextField;
