import React, { Component } from 'react';
import TextField from './TextField.js';
import TextArea from './TextArea.js';

class EventView extends Component {

  handleSelectedEventTimeChange = (time) => {
    this.props.event.time = time;
    this.props.onChange(this.props.event);
  }

  handleSelectedEventCallsignChange = (callsign) => {
    this.props.event.callsign = callsign;
    this.props.onChange(this.props.event);
  }

  handleSelectedEventLocationChange = (location) => {
    this.props.event.location = location;
    this.props.onChange(this.props.event);
  }

  handleSelectedEventDescriptionChange = (description) => {
    this.props.event.description = description;
    this.props.onChange(this.props.event);
  }

  render() {
    if (this.props.event) {
      this.props.event.location = this.props.event.location || ""
      return <div className="two-columns">
              <div>
              <TextField label="Heure" id="selected-event-time" value={this.props.event.time} onChange={this.handleSelectedEventTimeChange} />
              <TextField label="Indicatif" id="selected-event-callsign" value={this.props.event.callsign} onChange={this.handleSelectedEventCallsignChange} />
              <TextField label="Position" id="selected-event-position" value={this.props.event.location} onChange={this.handleSelectedEventLocationChange} />
               </div>
               <TextArea label="Évènement" value={this.props.event.description} onChange={this.handleSelectedEventDescriptionChange} /><br />
               
            </div>
    } else {
      return <p>Aucun évènement sélectionné</p>
    }
  }

}

class EventsView extends Component {
  constructor(props) {
      super(props);
      this.state = { selectEvent:null }
  }


  componentWillReceiveProps(nextProps) {
    if (nextProps.events.every(event => this.props.events.includes(event)) == false) {
     this.setState({selectedEvent : null})
    }
  }


  selectEvent = (event) => this.setState({ selectedEvent: event });

  handleSelectedEventChange = (event) => {
    this.props.onChange(this.props.events);
  }

  addEvent = () => {
    let events = this.props.events
    let newEvent = {time:"XX:XX", callsign:"", description:""}
    events.push(newEvent)
    this.props.onChange(events)
    this.setState({selectedEvent:newEvent})
  }

  deleteSelectedEvent = () => {
    if(this.state.selectedEvent !== null) {
      let events = this.props.events
      let selectionIndex = events.indexOf(this.state.selectedEvent)
      events.splice(selectionIndex,1)
      this.props.onChange(events)
      if (selectionIndex > 0) {
        this.selectEvent(this.props.events[selectionIndex - 1])
      } else {
        this.setState({selectedEvent:null})
      }
    }
  }
    
  render() {
    return <div>
      <div className="toolbar">
        <button className="action" onClick={this.addEvent}>Ajouter</button>
        <button className={`action${this.state.selectedEvent == null ? " disabled" : ""}`} onClick={this.deleteSelectedEvent}>Supprimer</button>
      </div>
      <div className="master">
        <table>
          <thead>
            <tr>
              <th>
                Heure
              </th>
              <th>
                Indicatif
              </th>
              <th>
                Position
              </th>
              <th>
                Évènement
              </th>
            </tr>
          </thead>
          <tbody>
        {this.props.events.map(event => {
          return <tr onClick={() => this.selectEvent(event)} className={`${this.state.selectedEvent === event ? " selected" : ""}`}>
                  <td>
                    {event.time}
                  </td>
                  <td>
                    {event.callsign}
                  </td>
                  <td>
                    {event.location}
                  </td>
                  <td>
                    {event.description}
                  </td>
                </tr>
        })}
          </tbody>
        </table>
      </div>
      <div className="detail">
        <EventView event={this.state.selectedEvent} onChange={this.handleSelectedEventChange} />
      </div>
      </div>
  }
}

export default EventsView;
